import * as React from 'react';
import { GatsbyImage } from 'gatsby-plugin-image';
import { Link } from 'gatsby';
import { find } from 'lodash';
import { e2p } from '~src/_utils/utils';

export const ProductCard = ({ product }) => {
  const status = find(product?.attributes?.nodes, { name: 'status' })?.options;

  return (
    <article className="tm-product-card uk-height-1-1">
      <div className="tm-product-card-media">
        <div className="tm-ratio tm-ratio-4-3">
          <Link className="tm-media-box" to={`${product.uri}`}>
            {/* Labels */}
            {status && (
              <div className="tm-product-card-labels">
                {status.map(tag => {
                  switch (tag) {
                    case 'جدید':
                      return (
                        <span key={tag} className="uk-label uk-label-success uk-margin-xsmall-left">
                          {tag}
                        </span>
                      );

                    case 'top selling':
                      return (
                        <span key={tag} className="uk-label uk-label-warning uk-margin-xsmall-left">
                          {tag}
                        </span>
                      );

                    case 'ویژه':
                      return (
                        <span key={tag} className="uk-label uk-label-danger uk-margin-xsmall-left">
                          {tag}
                        </span>
                      );

                    default:
                      return (
                        <span key={tag} className="uk-label">
                          {tag}
                        </span>
                      );
                  }
                })}
              </div>
            )}
            {/* Image */}
            <figure className="tm-media-box-wrap">
              {product?.image ? (
                <GatsbyImage alt={product.name} image={product.image.localFile.childImageSharp.gatsbyImageData} />
              ) : (
                <span className="uk-text-muted" data-uk-icon={`icon: image ; ratio: 3`}></span>
              )}
            </figure>
          </Link>
        </div>
      </div>
      {/* Body */}

      <div className="tm-product-card-body">
        <div className="tm-product-card-info">
          <div className="uk-text-meta uk-margin-xsmall-bottom"> {product.productCategories.nodes[0].name}</div>
          <h3 className="tm-product-card-title">
            <Link className="uk-link-heading" to={`${product.uri}`}>
              {product.name}
            </Link>
          </h3>
          {/* Properties */}
        </div>
        {/* Shop */}
        <div className="tm-product-card-prices">
          {product?.price ? (
            <>
              {product.oldPrice && <del className="uk-text-meta">product.oldPrice</del>}
              <div className="tm-product-card-price uk-text-small">{e2p(product.price.replace('-', 'تا'))}</div>
            </>
          ) : (
            <div className="uk-text-muted uk-text-success">تماس بگیرید</div>
          )}
        </div>
      </div>
    </article>
  );
};
